const CreateObject = () => {
  const navigate = useNavigate();
  const [index, setIndex] = useState(0);
  const {objectId} = useParams();


  const onSubmitForm = (values) => {
    const body = {};
    Object.keys(values).forEach(key => {
      if (values[key] instanceof moment) {
        body[key] = values[key].format("DD.MM.YYYY");
      } else {
        body[key] = values[key];
      }
    })
    body.object = objectId;

    axios.post(`${MainApi}/category`, body)
      .then(() => {
        toast.success("successfully saved object");
        navigate(`/houses/${objectId}`);
      }).catch(() => {
      toast.error("error");
    })
  };

  const onFailedForm = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };


  return (
    <div>
      <>
        <Tabs
          index={index}
          setIndex={setIndex}
          tabs={[
            {
              title: "Uy qo'shish",
              content: (
                <Form
                  name="basic"
                  initialValues={{remember: true}}
                  onFinish={onSubmitForm}
                  onFinishFailed={onFailedForm}
                  autoComplete="off"
                  layout="vertical"
                  wrapperCol={{span: 16}}
                >
                  <Form.Item
                    label="Nomi"
                    name="name"
                    rules={[{required: true, message: "*"}]}
                  >
                    <Input/>
                  </Form.Item>
                  <Form.Item
                    label="Tip"
                    name="tip"
                    rules={[{required: true, message: "*"}]}
                  >
                    <Input/>
                  </Form.Item>

                  <Form.Item>
                    <Button type="primary" htmlType="submit">
                      Submit
                    </Button>
                  </Form.Item>
                </Form>
              ),
            },
          ]}
        />
      </>
    </div>
  );
};

export default CreateObject;
